@import '_vars_and_mixins.scss';
@import '_fonts.scss';

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body{
    height: 100%;
}

body{
    font-family: $p_reg;
    font-size: 14px;
    overflow-x: hidden;
}

ul{
    margin: 0;
    padding: 0;
    list-style: none;
}
a{
    text-decoration: none;
    color: $base-text-color;
}

button{
    cursor: pointer;
}

.wrapper{
    height: 100%;
    display: flex;
    flex-direction: column;
}

main{
    flex: 1 0 auto;
}

footer{
    flex: 0 0 auto;
}

.box{
    max-width: 1200px;
    padding: 0 12px;
    margin: 0 auto 0 auto;
}

.colored{
    background: #f9f8f8;
}

.blur_bg_box{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 2;
    background: rgba(0,0,0,.3);
    background-attachment: fixed;
    filter: blur(10px);
}

.btn-disabled{
    border: solid 1px rgba(0, 0, 0, 0.1);
    background: #ced2db;
    box-shadow: none;
}

h1{
    font-size: 26px;
    line-height: 1.38;
}
h2{
    font-size: 22px;
    line-height: 1.45;
}

.crumbs{
    padding: 12px;
    ul{
        display: flex;
        li{
            a{
                font-family: $p_semi;
                line-height: 1.5;
                display: inline-block;
                position: relative;
                color: $base-btn-color;
                margin-right: 28px;
                &:after{
                    position: absolute;
                    content: '';
                    width: 8px;
                    height: 8px;
                    top: calc(50% - 4px);
                    right: -20px;
                    border: 4px solid transparent;
                    border-left: 8px solid #7f8fa4;
                }
                &.actual-page{
                    pointer-events: none;
                    color: #7f8fa4;
                    font-family: $p_reg;
                    &:after{
                        display: none;
                    }
                }
            }
        }
    }
}

